export function getAssetsUrl(url: string) {
  const jsdelivrCDN =
    "https://cdn.jsdelivr.net/gh/YunYouJun/air-conditioner/public";
  return (isProd ? jsdelivrCDN : process.env.PUBLIC_URL) + url;
}
